<template>
  <b-modal v-if="$page.props.user != null" v-model="showModal" no-close-on-backdrop hide-footer body-class="pb-0" :title="'  ' + user.name" size="md" :centered="true">
    <p>
        <span class="mx-1">{{ user.first_name }}</span>  <span class="mx1">{{ skill.name }}</span>   
    </p>
    <RadioButton
      class="d-flex justify-content-around"
      :values="[
        { label: '', value: '1' },
        { label: '', value: '2' },
        { label: '', value: '3' },
      ]"
      v-model="level"
    ></RadioButton>
    <div class="w-100">
      <h4>     {{ user.first_name }} </h4>
      <v-select
        ref="reasonSelect"
        class="datepicker-list w-100"
        :placeholder="' '"
        label="label"
        dir="rtl"
        v-model="reason"
        :options="[
          {
            label: '      ',
            value: '      ',
          },
          {
            label: '    ' + user.first_name + ' /',
            value: '    ' + user.first_name + ' /',
          },
          {
            label: '       ',
            value: '       ',
          },
          {
            label: '  ' + user.first_name + ' ',
            value: '  ' + user.first_name + ' ',
          },
          {
            label: '    ',
            value: '    ',
          },
        ]"
      >
        <template #open-indicator="{ attributes }">
          <span v-bind="attributes">
            <i class="material-icons">keyboard_arrow_down</i>
          </span>
        </template>
        <template #no-options>  </template>
      </v-select>
    </div>
    <div class="w-100 d-flex mt-3 justify-content-center">
      <loading-button :loading="loading" @click.native="onCredit" class="btn btn-dark"></loading-button>
    </div>
  </b-modal>
</template>

<script>
import ModalMixin from "../../Mixins/Modal";
import RadioButton from "../inputs/RadioButton";
export default {
  methods: {
    onCredit() {
      if (this.reason != null) {
        this.loading = true;

        axios
          .post("/skills/credit", {
            skill: this.skill.id,
            level: this.level,
            reason: this.reason.value,
          })
          .then((response) => {
            if (response.data.result) {
              this.$emit("credited");
              this.$emit("update:show", false);
            } else {
              this.handleError(response.data.errors);
            }
          })
          .catch((errors) => {})
          .then(() => (this.loading = false));
      } else {
        this.toast("   ");
      }
    },
  },
  components: {
    RadioButton,
  },
  data() {
    return {
      level: undefined,
      reason: null,
      loading: false,
    };
  },
  props: {
    skill: {
      type: Object,
      default: undefined,
      required: true,
    },
    user: {
      type: Object,
      default: undefined,
      required: true,
    },
  },
  mixins: [ModalMixin],
  name: "SkillCreditModal",
};
</script>

<style></style>
